import React from "react";
import "./App.css";

import Menu from "./Pages/Menu/Menu";
import PreQuestion from "./Pages/PreQuestion/PreQuestion";
import First from "./Pages/First/First";
import Second from "./Pages/Second/Second";
import Third from "./Pages/Third/Third";
import Fourth from "./Pages/Fourth/Fourth";
import Fifth from "./Pages/Fifth/Fifth";
import Sixth from "./Pages/Sixth/Sixth";
import FacebookWin from "./Pages/FacebookWin/FacebookWin";
import YoutubeWin from "./Pages/YoutubeWin/YoutubeWin";
import TwitchWin from "./Pages/TwitchWin/TwitchWin";

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeComponent: 3,
      userName: "",
      answers: [],
      youtube: 0,
      facebook: 0,
      twitch: 0,
      winner: null
    };
  }

  switchBackgroundStyle = () => {
    const { youtube, twitch, facebook } = this.state;

    if (youtube > twitch) {
      if (youtube > facebook) {
        return "menu-wrapper youtube";
      } else {
        return "menu-wrapper facebook";
      }
    } else {
      if (twitch > facebook) {
        return "menu-wrapper twitch";
      } else {
        return "menu-wrapper facebook";
      }
    }
  };

  handleActiveScreen = () => {
    const { activeComponent, userName, answers } = this.state;

    switch (activeComponent) {
      default:
        return;
      case 3:
        return (
          <Menu
            startInfographic={this.next}
            setName={this.setName}
            name={userName}
          />
        );
      case 4:
        return (
          <PreQuestion
            changeScreen={this.next}
            answer={this.addAnswer}
            goBack={this.goBack}
          />
        );
      case 5:
        return (
          <First
            previousAnswer={answers[0]}
            changeScreen={this.next}
            answer={this.questionAnswer}
            goBack={this.goBack}
          />
        );
      case 6:
        return (
          <Second
            previousAnswer={answers[1]}
            changeScreen={this.next}
            answer={this.questionAnswer}
            goBack={() => this.goBack()}
          />
        );
      case 7:
        return (
          <Third
            previousAnswer={answers[2]}
            platform={answers[1]}
            end={this.end}
            changeScreen={this.next}
            answer={this.questionAnswer}
            goBack={() => this.goBack()}
          />
        );
      case 8:
        return (
          <Fourth
            previousAnswer={answers[3]}
            platform={answers[1]}
            changeScreen={this.next}
            answer={this.questionAnswer}
            end={this.end}
            goBack={() => this.goBack()}
          />
        );
      case 9:
        return (
          <Fifth
            previousAnswer={answers[4]}
            changeScreen={this.next}
            answer={this.questionAnswer}
            goBack={() => this.goBack()}
          />
        );
      case 10:
        return (
          <Sixth
            previousAnswer={answers}
            changeScreen={this.next}
            answer={this.questionAnswer}
            end={this.end}
            goBack={() => this.goBack()}
          />
        );
      case 0:
        return <FacebookWin name={userName} />;
      case 1:
        return <YoutubeWin name={userName} />;
      case 2:
        return <TwitchWin name={userName} />;
    }
  };

  setName = value => {
    this.setState({ userName: value });
  };

  addAnswer = value => {
    let tempAnswer = [];
    tempAnswer.push(value);
    this.setState({ answers: tempAnswer });
  };

  questionAnswer = (index, value) => {
    let temp = [];
    if (index === 1) {
      temp.push(null);
      temp.push({
        answer: value
      });
    } else {
      temp.push({
        answer: value
      });
    }
    this.setState(prevState => ({ answers: [...prevState.answers, temp] }));
  };

  next = value => {
    this.setState(prevState => ({
      activeComponent: prevState.activeComponent + value
    }));
  };

  end = value => {
    let points = "";
    switch (value) {
      default:
        return;
      case 0:
        points = "Facebook";
        break;
      case 1:
        points = "Youtube";
        break;
      case 2:
        points = "Twitch";
        break;
    }
    this.setState({
      activeComponent: value,
      winner: true,
      facebook: points === "Facebook" ? 100 : 0,
      youtube: points === "Youtube" ? 100 : 0,
      twitch: points === "Twitch" ? 100 : 0
    });
  };

  goBack = () => {
    return this.setState(prevState => {
      let tempData = prevState.answers;
      tempData.pop();
      return {
        answers: tempData,
        activeComponent: prevState.activeComponent - 1
      };
    });
  };

  render() {
    const { winner } = this.state;
    return (
      <div className={winner ? this.switchBackgroundStyle() : `menu-wrapper`}>
        <div className={winner ? `menu-container-end` : `menu-container`}>
          {this.handleActiveScreen()}
        </div>
      </div>
    );
  }
}
