import React, { useState } from "react";
import "./First.css";
import ProgressBar from "react-bootstrap/ProgressBar";
const backBtn = require("../../Assets/botao_voltar_proximo.svg");
const pauseBtn = require("../../Assets/botao_pause.svg");
const twitch_icon = require("../../Assets/twitch_icon.svg");
const youtube_icon = require("../../Assets/youtube_icon.svg");
const facebook_icon = require("../../Assets/facebook_icon.svg");

export default function First({
  previousAnswer,
  changeScreen,
  answer,
  goBack
}) {
  const [active, setActive] = useState(null);
  const [_active, _setActive] = useState(null);
  const [move, setMove] = useState(false);

  const handleSubmit = () => {
    answer(1, active === 0 ? true : false);
    return changeScreen(1);
  };

  const _handleSubmit = () => {
    let temp = "";

    switch (_active) {
      default:
        return;
      case 0:
        temp = "Youtube";
        break;
      case 1:
        temp = "Facebook";
        break;
      case 2:
        temp = "Twitch";
        break;
    }

    answer(0, temp);
    return changeScreen(1);
  };

  return (
    <div
      className={move ? "container go-side-left" : "container go-side-right"}
      onAnimationEnd={
        move
          ? _active !== null
            ? () => _handleSubmit()
            : () => handleSubmit()
          : null
      }
    >
      <div className="game-content">
        <div className="question-container">
          <p className="question-title mb-4">
            {previousAnswer
              ? "Qual plataforma voc√™ utiliza?"
              : "Voc√™ j√° sabe que tipo de conte√∫do vai postar?"}
          </p>
        </div>
        {previousAnswer ? (
          <div className="button-container more-margin-bottom">
            <button
              className={
                _active === 0 ? "clicked-button-logo" : "yn-button-logo"
              }
              type="button"
              onClick={() => _setActive(0)}
            >
              <img
                src={youtube_icon}
                className="logo-social-media"
                alt="logo"
              />
              <p>Youtube</p>
            </button>
            <button
              className={
                _active === 1 ? "clicked-button-logo" : "yn-button-logo"
              }
              type="button"
              onClick={() => _setActive(1)}
            >
              <img
                src={facebook_icon}
                className="logo-social-media"
                alt="logo"
              />
              <p>Facebook</p>
            </button>
            <button
              className={
                _active === 2 ? "clicked-button-logo" : "yn-button-logo"
              }
              type="button"
              onClick={() => _setActive(2)}
            >
              <img src={twitch_icon} className="logo-social-media" alt="logo" />
              <p>Twitch TV</p>
            </button>
          </div>
        ) : (
          <div className="button-container">
            <button className="button-box" onClick={() => setActive(0)}>
              <p className={active === 0 ? "clicked-button" : "yn-button"}>
                Sim
              </p>
            </button>
            <button className="button-box" onClick={() => setActive(1)}>
              <p className={active === 1 ? "clicked-button" : "yn-button"}>
                N√£o
              </p>
            </button>
          </div>
        )}
        <div className="progress-bar">
          <ProgressBar
            animated
            variant="success"
            now={10}
            style={{ marginBottom: 20 }}
          />
        </div>
        <div className="player-button-container">
          <button onClick={() => goBack()}>
            <img
              className="player-button"
              alt="Imagem de voltar de um player"
              src={backBtn}
            />
          </button>
          {/* <img
            className="player-button"
            alt="Bot√£o de pause de um player"
            src={pauseBtn}
          /> */}
          <button onClick={() => setMove(true)}>
            <img
              className="player-button-right"
              alt="Imagem de ir para frente de um player"
              src={backBtn}
            />
          </button>
        </div>
      </div>
    </div>
  );
}
