import React, { useState } from "react";
import ProgressBar from "react-bootstrap/ProgressBar";
const backBtn = require("../../Assets/botao_voltar_proximo.svg");
const pauseBtn = require("../../Assets/botao_pause.svg");

export default function Third({
  previousAnswer,
  platform,
  changeScreen,
  answer,
  end,
  goBack
}) {
  const [active, setActive] = useState(null);
  const [_active, _setActive] = useState(null);
  const [move, setMove] = useState(false);
  const choosenPlatform = platform[0] !== null ? platform[0].answer : null;

  const handleSubmit = () => {
    answer(1, active === 1 ? false : true);
    return changeScreen(1);
  };

  const _handleSubmit = () => {
    if (_active === 1) {
      let final = 0;
      switch (choosenPlatform) {
        default:
          return;
        case "Facebook":
          final = 0;
          break;
        case "Youtube":
          final = 1;
          break;
        case "Twitch":
          final = 2;
          break;
      }

      return end(final);
    }
    answer(0, true);
    return changeScreen(1);
  };

  return (
    <div
      className={move ? "container go-side-left" : "container go-side-right"}
      onAnimationEnd={
        move
          ? active === null
            ? () => _handleSubmit()
            : () => handleSubmit()
          : null
      }
    >
      <div className="game-content">
        <div className="question-container">
          <p className="question-title">
            {previousAnswer[0] === null
              ? previousAnswer[1].answer
                ? "Voc√™ pretende postar conte√∫do Ao vivo ou On demand?"
                : ""
              : "Voc√™ est√° disposto a publicar conte√∫do em duas plataformas?"}
          </p>
        </div>
        <div className="button-container">
          {previousAnswer[0] === null ? (
            previousAnswer[1].answer ? (
              <div className="button-container">
                <button className="button-box" onClick={() => setActive(0)}>
                  <p className={active === 0 ? "clicked-button" : "yn-button"}>
                    Ao vivo
                  </p>
                </button>
                <button className="button-box" onClick={() => setActive(1)}>
                  <p className={active === 1 ? "clicked-button" : "yn-button"}>
                    On demand
                  </p>
                </button>
              </div>
            ) : (
              ""
            )
          ) : (
            <div className="button-container">
              <button className="button-box" onClick={() => _setActive(0)}>
                <p className={_active === 0 ? "clicked-button" : "yn-button"}>
                  Sim
                </p>
              </button>
              <button className="button-box" onClick={() => _setActive(1)}>
                <p className={_active === 1 ? "clicked-button" : "yn-button"}>
                  N√£o
                </p>
              </button>
            </div>
          )}
        </div>
        <div className="progress-bar">
          <ProgressBar
            animated
            variant="success"
            now={35}
            style={{ marginBottom: 20 }}
          />
        </div>
        <div className="player-button-container">
          <button onClick={() => goBack()}>
            <img
              className="player-button"
              alt="Imagem de voltar de um player"
              src={backBtn}
            />
          </button>
          {/* <img
            className="player-button"
            alt="Bot√£o de pause de um player"
            src={pauseBtn}
          /> */}
          <button onClick={() => setMove(true)}>
            <img
              className="player-button-right"
              alt="Imagem de ir para frente de um player"
              src={backBtn}
            />
          </button>
        </div>
      </div>
    </div>
  );
}
